import { isNotInUni } from '../utils/utils';

function innerHideKeyboard() {
  const { activeElement } = document;
  if (activeElement && (activeElement.tagName === 'TEXTAREA' || activeElement.tagName === 'INPUT')) {
    activeElement.blur();
  }
}


export function hideKeyboard() {

  if (isNotInUni()) {
    innerHideKeyboard();
  } else {
    uni.hideKeyboard();
  }





}
